<template>
  <div class="loginForm" id="loginForm">
    <div class="userNameInpt">
      <input type="text" placeholder="please type a name...">
    </div>
    <div class="userIcon">
      <ul v-if="allUserVisable">
        <li v-for="(user, index) in users" :key="user.id">
          <img @click="onfocusUserHandel(user.Icon, user.description, user.id, user.name, index)" :src="user.Icon"
            :alt="user.description" :userID="user.id" :title="user.description" :index="index">
          <p>{{ user.name }}</p>
        </li>
      </ul>
      <ul v-if="onfocusUserVisable">
        <li>
          <img v-bind:src="onfocusUser.Icon" v-bind:alt="onfocusUser.description" v-bind:userID="onfocusUser.id"
            v-bind:title="onfocusUser.description" v-bind:index="onfocusUser.index">
          <p>{{ onfocusUser.name }}</p>
        </li>
      </ul>
    </div>
    <div>
      <button class="loginBtn" @click="loginHandel()">登陆</button>
      <button class="registerBtn" @click="signUpHandel()">添加</button>
      <button class="allUserBtn" @click="allUserHandel()">所有用户</button>
    </div>
  </div>
</template>
<script setup>
import { defineProps, ref, defineEmits } from 'vue'
const props = defineProps(['users', 'Io'])

let onfocusUser = ref({})
let onfocusUserVisable = ref(false)
let allUserVisable = ref(true)
const emit = defineEmits(['login'])


let loginHandel = () => {
  if (onfocusUser.value !== {}) {
    props.Io.emit("login", onfocusUser.value)
    emit('login', onfocusUser.value)
  } else {
    alert("please choose a login")
  }
}
let allUserHandel = () => {
  onfocusUser.value = {}
  onfocusUserVisable.value = false
  allUserVisable.value = true
}

let onfocusUserHandel = (Icon, description, id, name, index) => {
  onfocusUser.value = { Icon, description, id, name, index }
  onfocusUserVisable.value = true
  allUserVisable.value = false
}
</script>
