<template>
  <div id="app" class="container">
    <chatLogin v-if="loginVisable" :users="users" :Io="Io" @login="login" />
    <chatMain v-if="mainVisable" :user="onFoucsUser" :Io="Io" />
    <chatSignUp v-if="signVisable" />
  </div>
</template>

<script setup>
import chatLogin from './components/chatLogin'
import chatMain from './components/chatMain'
import chatSignUp from './components/chatSignUp'
import { socketMap } from './options/socket'
import { ref, onMounted, onUnmounted, } from 'vue'
import { io } from 'socket.io-client'

let mainVisable = ref(false)
let signVisable = ref(false)
let loginVisable = ref(false)
let Io = io.connect(socketMap.addr)
let users = ref({})
let onFoucsUser = ref({})

let login = (user) => {
  onFoucsUser.value = user
  mainVisable.value = true
  loginVisable.value = false
}


onMounted(() => {
  try {
    Io.on("bordercaseJoin", (data) => {
      users.value = JSON.parse(data)
      loginVisable.value = true
    })
  } catch (e) {
    console.log(e);
  }
})

onUnmounted(() => {
  Io.emit('disconnect')
})
</script>

<style lang="scss" scoped></style>
